const getNameFromNameAndEmail = (nameAndEmail) => {
    const fileds = nameAndEmail.split("|");
    const fullname = fileds[0];
    return fullname
}

export const assignEditorTemplate = (submisisonName, chiefEditorName, editorName) => {
    const email =
        '<p>Xin cho <strong>' + getNameFromNameAndEmail(editorName) + '</strong>.</p>' +
        '<p>Ban bin tp tp ch truy cp m VNOJS xin c thng bo rng bn  c ch nh ' +
        'vo v tr bin tp vin ch tr thm nh bi bo <strong>' + submisisonName + '</strong></>' +
        '. Bn vui lng truy cp vo h thng  xem chi tit yu cu v ch tr qu trnh thm nh.' +
        ' Tin trnh thm nh s bt u khi bn xc nhn trn h thng.</p>' +
        '<p>Chng ti tin rng bn l ngi ph hp vi v tr bin tp vin cho bi bo ny.</p>' +
        '<p>Trn trng <br/>Tng bin tp</p>' +
        '<p><strong>' + chiefEditorName + '</strong></p>';
    return email;
}

