import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getAllUsers } from '../../../store/actions/userActions';
import { getMyNotifications } from '../../../store/actions/authActions';
import ContentHeader from '../Shared/ContentHeader';
import Spinner from '../../UI/Spinner/Spinner';
import Aux from '../../../hoc/Auxiliary/Auxiliary';
import { Link } from 'react-router-dom';
import Pagination from '../../UI/Pagination/Pagination';

const ITEMS_PER_PAGE = 8;
class Home extends Component {

    componentDidMount() {
        this.props.getAllUsers();
    }

    componentDidUpdate(prevProps) {
        if (this.props.location.search !== prevProps.location.search) {
            const prevQuery = new URLSearchParams(prevProps.location.search);
            const query = new URLSearchParams(this.props.location.search);
            const prevPage = prevQuery.get('page');
            const page = query.get('page');
            if (page !== prevPage) {
                this.props.getAllUsers(page, ITEMS_PER_PAGE);
            }
        }
    }

    refreshHandler = () => {
        this.props.getAllUsers();
        this.props.getAllRoles();
        this.props.getMyNotifications();
    }

    render() {
        return (
            <div className="content-wrapper">
                <section className="content-header">
                    <ContentHeader title="Qun l ngi dng">
                        <li className="breadcrumb-item">Qun l ngi dng</li>
                    </ContentHeader>
                </section>

                <section className="content">
                    {!this.props.loading ? (
                        <div className="card">
                            <div className="card-header">
                                <h3 className="card-title">Thng tin ngi dng</h3>
                                <div className="float-right mr-5">
                                    <button className="btn btn-tool" onClick={this.refreshHandler}>
                                        <i className="fas fa-sync-alt" style={{ fontSize: '20px' }}></i>
                                    </button>
                                </div>
                            </div>

                            <div className="card-body p-0">
                                {this.props.users.length > 0 ? (
                                    <Aux>
                                        <table className="table table-hover projects">
                                            <thead>
                                                <tr>
                                                    <th style={{ width: '1%' }}> #</th>
                                                    <th style={{ width: '29%' }}> H tn </th>
                                                    <th style={{ width: '15%' }}> Username</th>
                                                    <th style={{ width: '20%' }}> Email</th>
                                                    <th style={{ width: '15%' }}> Vai tr</th>
                                                    <th style={{ width: '20%' }} className="text-center"> Xem chi tit</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {this.props.users.map(user => (
                                                    <Aux key={user._id}>
                                                        <tr data-toggle="collapse" data-target={`#aaa${user._id}`} className="accordion-toggle" aria-expanded="true" aria-controls="collapseOne">
                                                            <td style={{ cursor: 'pointer' }}><i className="fas fa-caret-down"></i></td>
                                                            <td><b>{user.lastname} {user.firstname}</b></td>
                                                            <td>{user.username}</td>
                                                            <td>{user.email}</td>
                                                            <td>{user.role.name}</td>
                                                            <td className="project-actions text-center">
                                                                <Link to={`/dashboard/users/${user._id}`} className="btn btn-outline-dark btn-sm mr-1 btn-flat">
                                                                    <i className="fas fa-eye"></i> Xem chi tit
                                                                </Link>
                                                            </td>
                                                        </tr>
                                                        {/* sub tab */}
                                                        <tr>
                                                            <td colSpan="8" className="hiddenRow">
                                                                <div id={`aaa${user._id}`} className="accordian-body collapse">
                                                                    <div className="col-lg-12">
                                                                        <div className="row pl-5">
                                                                            <div className="col-lg-4 pt-2">

                                                                            </div>
                                                                            <div className="col-lg-4 border rounded pt-2">

                                                                            </div>
                                                                            <div className="col-lg-4 border rounded pt-2 pb-2">

                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </td>
                                                        </tr>
                                                    </Aux>
                                                ))}
                                            </tbody>
                                        </table>
                                        <div className="mt-2">
                                            <Pagination
                                                currentPage={this.props.currentPage}
                                                hasNextPage={ITEMS_PER_PAGE * this.props.currentPage < this.props.total}
                                                hasPrevPage={this.props.currentPage > 1}
                                                nextPage={this.props.currentPage + 1}
                                                prevPage={this.props.currentPage - 1}
                                                lastPage={Math.ceil(this.props.total / ITEMS_PER_PAGE)}
                                                location={this.props.location} />
                                        </div>
                                    </Aux>
                                ) : <div>Khng tm thy thng tin ngi dng.</div>}
                            </div>
                        </div>
                    ) : <Spinner />}
                </section>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        users: state.user.users,
        roles: state.user.roles,
        loading: state.user.loading,
        error: state.user.error,
        total: state.user.total_items,
        currentPage: state.user.currentPage,
    };
};

const mapDispatchToProps = {
    getAllUsers,
    getMyNotifications
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);