import React from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import Aux from '../../../../hoc/Auxiliary/Auxiliary';
import { STAGE } from '../../../../utils/constant';

const SubmissionActions = (props) => {
    return (
        <Aux>
            {props.userId === props.submission.authorId._id ? (
                <div className="form-group">
                    <label>Chnh sa</label><br />
                    {props.submission.stageId.value === STAGE.SUBMISSION.value ? (
                        <Aux>
                            <Link to={`/dashboard/edit-submission/${props.submission._id}`} className="btn btn-outline-secondary btn-sm mr-1">
                                <i className="fas fa-pencil-alt"></i> Chnh sa
                            </Link>
                            <button className="btn btn-outline-danger btn-flat btn-sm" data-toggle="modal" data-target="#deleteSubmissionModal">
                                <i className="fas fa-trash"></i> Xa bi bo
                            </button>
                        </Aux>
                    ) : props.submission.stageId.value === STAGE.REVIEW.value && props.authorAssignment ? (
                        <Aux>
                            <Link to={`/dashboard/revise-submission/${props.submission._id}`} className="btn btn-outline-secondary btn-sm mr-1">
                                <i className="fas fa-pencil-alt"></i> Np bn chnh sa bi bo
                            </Link>
                        </Aux>
                    ) : (
                        <Aux>
                            <button className="btn btn-outline-secondary btn-sm mr-1 btn-flat"
                                onClick={() => toast.error('Khng th chnh sa bi bo khi cha c yu cu.')}>
                                        <i className="fas fa-pencil-alt"></i> Chnh sa
                            </button>
                            <button className="btn btn-outline-danger btn-sm btn-flat"
                                onClick={() => toast.error('Khng th xa bi bo trong thi gian thm nh.')}>
                                        <i className="fas fa-trash"></i> Xa bi bo
                            </button>
                        </Aux>
                    )}
                </div>
            ) : null}
        </Aux>
    );
};

export default SubmissionActions;